import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: { // dataのようなもの
    currentItems: [],
    hourlyItems: [],
    sunsetTime: [],
    sunriseTime: [],
    nextSunriseTime: []
  },
  getters: { // 算出プロパティ

  },
  mutations: { // methodsのようなもの
    setCurrentWeatherData(state, currentItems) {
      state.currentItems = currentItems
    },
    setHourlyWeatherData(state, hourlyItems) {
      state.hourlyItems = hourlyItems
    },
    setSunsetTime(state, sunsetTime) {
      state.sunsetTime = sunsetTime
    },
    setSunriseTime(state, sunriseTime) {
      state.sunriseTime = sunriseTime
    },
    setNextSunriseTime(state, nextSunriseTime) {
      state.nextSunriseTime = nextSunriseTime
    }
  },
  actions: { // 非同期処理
    getCurrentWeatherData({commit}) { // 現在の気象データ(current)を取得
      return axios.get(
        "https://api.openweathermap.org/data/2.5/onecall?lat=34.40&lon=135.31&units=metric&exclude=minutely,hourly,daily,alerts&appid=b6941b0f378debcf43eaacabc690b3be&lang=ja"
        )
                  .then(response => { // 現在の天気(current.weather.main)を取得
                    commit('setCurrentWeatherData', response.data.current)
                    // current.weather.id = 気象条件ID(詳細)
                    // current.weather.main = 気象パラメータのグループ
                  })
    },
    getHourlyWeatherData({commit}) { // 1時間ごとの天気予報データ(hourly)を取得
      return axios.get(
        "https://api.openweathermap.org/data/2.5/onecall?lat=34.40&lon=135.31&units=metric&exclude=current,minutely,daily,alerts&appid=b6941b0f378debcf43eaacabc690b3be&lang=ja"
        )
                  .then(response => {
                    // console.log(response.data.hourly)
                    commit('setHourlyWeatherData', response.data.hourly)
                    // hourly.dt = 予測データの時刻。Unixtimeで表示される
                    // hourly.temp = 温度
                    // hourly.weather.id = 気象条件ID
                    // hourly.weather.main = 気象パラメータのグループ
                    // 24時間後までループで回す？
                  })
    },
    getDailyWeatherData({commit}) { // 毎日の天気予報データ(daily)を取得（翌日の日の出時間用）
      return axios.get (
        "https://api.openweathermap.org/data/2.5/onecall?lat=34.40&lon=135.31&units=metric&exclude=current,minutely,hourly,alerts&appid=b6941b0f378debcf43eaacabc690b3be&lang=ja"
      )
                  .then(response => {
                    commit('setSunsetTime', response.data.daily[0].sunset)
                    commit('setSunriseTime', response.data.daily[0].sunrise)
                    commit('setNextSunriseTime', response.data.daily[1].sunrise)
                    // console.log(response.data.daily)
                    // daily[1].sunrise = 翌日の日の出時間([0]は当日)
                  })
    }
  },
  modules: {
  }
})
