<template>
  <div>
    <div>
      <p class="time" v-html="time"></p>
      <p class="sec">{{ nowsec }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: '',
      nowsec: ''
    }
  },
  created() {
    let that = this
    setInterval(that.getTime, 500)
  },
  methods: {
    getTime() {
      let today = new Date()
      let hour = ("0"+today.getHours()).slice(-2);
      let min = ("0"+today.getMinutes()).slice(-2);
      let sec = ("0"+today.getSeconds()).slice(-2)
      this.time = `${hour}<span>:</span>${min}`
      this.nowsec = sec
    }
  }
}
</script>

<style scoped>
div {
  clear:both;
  margin-bottom: 2vh;
}
.time {
  width: 60%;
  padding-top: 3vh;
  margin: 0 auto;
  font-size: 20vh;
  overflow-wrap: break-word;
  line-height: 1;
}
/* v-htmlを使ったタグにCSSを効かせるには「>>>」が必要 */
.time >>> span {
  display: none;
}
.sec {
  width: 50%;
  margin: 0 auto;
  font-size: 7vh;
  overflow-wrap: break-word;
}
/* デバイスが横向きの時のスタイル */
@media (orientation: landscape) {
  div {
    width: 100vw;
    display: flex;
  }
  .time {
    width: 80%;
    height: 43vh;
    padding: 0;
    font-size: 43vh;
    margin-top: 3rem;
    margin-right: 0;
  }
  .time >>> span {
    display: inline;
  }
  .sec {
    width: 20%;
    height: 43vh;
    margin-top: 3rem;
    margin-left: 0;
    font-size: 13vh;
    text-align: left;
    line-height: 65vh;
  }
}
/* タブレット縦向きのスタイル */
@media screen and (min-width:600px) {
  .time {
    width: 40%;
    font-size: 20vh;
  }
}
/* タブレット横向きのスタイル */
@media screen and (min-width:600px) and (orientation:landscape) {
  .time {
    width: 80%;
    font-size: 33vh;
    line-height: 55vh;
  }
  .sec {
    font-size: 10vh;
    line-height: 69vh;
  }
}
/* PC、横幅>縦幅のスタイル */
@media screen and (min-width:1025px) and (orientation:landscape) {
  .time {
    width: 80%;
