<template>
  <div id="app">
    <Loading v-show="loading"></Loading>
    <div v-show="!loading">
      <div id="container" :style="{backgroundImage:`url(${backImage})`}">
        <SettingMenu/> <!-- 設定ボタン -->
        <Clock/> <!-- 時計 -->
        <Weather/> <!-- 天気予報（縦表示の場合のみ） -->
        <div class="bottomSet">
          <DateTime/> <!-- 日時 -->
          <SunRS/> <!-- 日の出、日没時間 -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Clock from '@/components/Clock.vue'
import DateTime from '@/components/DateTime.vue'
import SettingMenu from '@/components/SettingMenu.vue'
import Weather from '@/components/Weather.vue'
import SunRS from '@/components/SunRS.vue'
import Loading from '@/components/Loading.vue'

export default {
  name: 'App',
  data() {
    return {
      index: ['1', '2', '3', '4', '5'],
      loading: true,
    }
  },
  computed: {
    currentItems() {
      return this.$store.state.currentItems
    },
    hourlyItems() {
      return this.$store.state.hourlyItems
    },
    sunsetTime() {
      return this.$store.state.sunsetTime
    },
    sunriseTime() {
      return this.$store.state.sunriseTime
    },
    nextSunriseTime() {
      return this.$store.state.nextSunriseTime
    },
    backImage() {
      let random = Math.floor(Math.random() * this.index.length)
      let todaySsetTime = this.sunsetTime
      let todaySriseTime = this.sunriseTime
      let nextSriseTime = this.nextSunriseTime
      let nowWeather = ''
      try {
        nowWeather = this.currentItems.weather[0].main
      } catch(e) {
        // console.log(e)
      }
      let nowtime = this.currentItems.dt
      // 日の出時間（日の出30分前〜日の出30分後）
      if(nowtime >= (todaySriseTime - 1800) && nowtime <= (todaySriseTime + 1800)) {
        return `"./back-images/sunrise/sunrise${random}.jpg"`
      // 日中（日の出30分後〜日没30分前）
      } else if (nowtime > (todaySriseTime + 1800) && nowtime < (todaySsetTime - 1800)) {
        if(nowWeather === 'Clouds') { // 曇り
          return `"./back-images/daytime/clouds/clouds${random}.jpg"`
        } else if (nowWeather === 'Clear') { // 晴れ
          return `"./back-images/daytime/clear/clear${random}.jpg"`
        } else if (nowWeather === 'Rain') { // 雨
          return `"./back-images/daytime/rain/rain${random}.jpg"`
        } else if (nowWeather === 'Snow') { // 雪
          return `"./back-images/daytime/snow/snow${random}.jpg"`
        } else if (nowWeather === 'Thunderstorm') { // 雷雨
          return `"./back-images/daytime/thunderstorm/thunderstorm${random}.jpg"`
        } else { // その他
          return "./back-images/daytime/fog.jpg"
        }
      // 日没時間（日没30分前〜日没30分後）
      } else if (nowtime >= (todaySsetTime - 1800) && nowtime <= (todaySsetTime + 1800)) {
        return `"./back-images/sunset/sunset${random}.jpg"`
      // 夜間（日没30分後〜日の出30分前）
      } else if (nowtime > (todaySsetTime + 1800) && (nowtime < nextSriseTime || nowtime < (todaySriseTime - 1800))) {
        if(nowWeather === 'Clouds') { // 曇り
          return `"./back-images/night/clouds/night-clouds${random}.jpg"`
        } else if (nowWeather === 'Clear') { // 晴れ
          return `"./back-images/night/clear/night-clear${random}.jpg"`
        } else if (nowWeather === 'Rain') { // 雨
          return `"./back-images/night/rain/night-rain${random}.jpg"`
        } else if (nowWeather === 'Snow') { // 雪
          return `"./back-images/night/snow/night-snow${random}.jpg"`
        } else if (nowWeather === 'Thunderstorm') { // 雷雨
          return `"./back-images/night/thunderstorm/night-thunderstorm${random}.jpg"`
        } else { // その他
          return "./back-images/daytime/night-fog.jpg"
        }
      // どれにも該当しない場合
      } else {
        return "./back-images/other.jpg"
      }
    }
  },
  methods: {
  },
  mounted() {
    this.$store.dispatch('getCurrentWeatherData'),
    this.$store.dispatch('getHourlyWeatherData'),
    this.$store.dispatch('getDailyWeatherData')
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  components: {
    Clock,
    DateTime,
    SettingMenu,
    Weather,
    SunRS,
    Loading
  }
}
</script>

<style>
#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#container {
  width: 100vw;
  height: 100vh;
  text-align: center;
  font-family: 'Cutive Mono', monospace;
  color: rgb(255, 255, 255);
  background-size: cover;
}
.bottomSet {
  display: flex;
  flex-flow: column;
  justify-content: center;
}
/* スマホ横向き時のスタイル */
@media (orientation: landscape) {
  .bottomSet {
    align-items: flex-end;
    margin-right: 4vw;
    margin-top: 12vh;
  }
}
/* タブレット横向き時のスタイル */
@media screen and (min-width:600px) and (orientation:landscape) {
  .bottomSet {
    margin-right: 8vw;
    margin-top: 20vh;
  }
}
</style>
