<template>
  <div>
    <p>{{ DateTime }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      DateTime: ''
    }
  },
  created() {
    this.getTime()
    let that = this
    setInterval(that.getTime, 500)
  },
  methods: {
    getTime() {
      let today = new Date()
      let week = ['Sun','Mon','Tue','Wed','Thu','Fri','Sat']
      let day = today.getDay()
      let month = today.getMonth() +1
      let date = today.getDate()
      this.DateTime = `${month}/${date} ${week[day]}`
    }
  },
}
</script>

<style scoped>
p {
  font-size: 5vh;
}
/* デバイスが横向きの時のスタイル */
@media (orientation:landscape) {
  p {
    font-size: 10vh;
  }
}
</style>
