<template>
  <div>
    <div id="setBtn" @click="activeBtn=!activeBtn">
      <img src="@/assets/icons/setting.svg" alt="setting">
    </div>
    <transition name="menu">
      <div class="settingMenu" v-show="activeBtn">
        <ul>
          <li class="acTitle">
            <button type="button" class="acTrigger" :class="open" @click="acToggle('1')">地域：大阪</button>
            <div class="acTarget" v-show="open.indexOf('1') >= 0">
              <div class="content">
                <transition name="content">
                  <p>設定</p>
                </transition>
              </div>
            </div>
          </li>
          <transition name="title">
            <li class="acTitle">
              <button type="button" class="acTrigger" :class="open" @click="acToggle('2')">表示項目</button>
              <div class="acTarget" v-show="open.indexOf('2') >= 0">
                <div class="content">
                  <p>表示項目</p>
                </div>
              </div>
            </li>
          </transition>
          <li class="acTitle">
            <button type="button" class="acTrigger" :class="{'state-open':open}" @click="acToggle('3')">文字色</button>
            <div class="acTarget" v-show="open.indexOf('3') >= 0">
              <div class="content">
                <p>文字色</p>
              </div>
            </div>
          </li>
          <li class="acTitle">
            <button type="button" class="acTrigger" :class="{'state-open':open}" @click="acToggle('4')">保存した設定</button>
            <div class="acTarget" v-show="open.indexOf('4') >= 0">
              <div class="content">
                <p>保存した設定</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeBtn: '',
      open: []
    }
  },
  methods: {
    acToggle(data) {
      if(this.open.indexOf(data) >= 0) {
        this.open = this.open.filter(n => n !== data)
      } else {
        this.open.push(data)
      }
    }
  }
}
</script>

