<template>
  <ul id="yohos">
    <li class="yoho" v-for="(hourlyItem, key) in limitCount" :key="key">
      <p>{{ createTime(hourlyItem.dt) }}</p>
      <!-- <img v-bind="createIcon(hourlyItem.weather[0].main, hourlyItem.dt)"> -->
      <p v-html="createIcon(hourlyItem.weather[0].main, hourlyItem.dt)"></p>
      <p>{{ createTemp(hourlyItem.temp) }}</p>
    </li>
  </ul>
</template>

<script>

export default {
  data() {
    return {
    }
  },
  computed: { // 算出プロパティ。引数もらえない
    hourlyItems() {
      return this.$store.state.hourlyItems
    },
    // 予報を表示する上限を設定
    limitCount() {
      return this.hourlyItems.slice(0, -24)
    },
    sunsetTime() {
      return this.$store.state.sunsetTime
    },
    sunriseTime() {
      return this.$store.state.sunriseTime
    },
    nextSunriseTime() {
      return this.$store.state.nextSunriseTime
    },
  },
  methods: {
    // unixtimeをxx:xxの形に変更
    createTime(dt) {
      let dateTime = new Date(dt * 1000)
      return dateTime.toLocaleTimeString('ja-JP').slice(0, -3)
    },
    // 気温を小数点以下切り捨てて「x°」の形に変更
    createTemp(temp) {
      let temperature = Math.floor(temp)
      return `${temperature}°`
    },
    // hourlyItem.weather.mainに応じたアイコンを表示
    createIcon(weather, dt) {
      if(weather == 'Clouds') {
        if(this.sunriseTime > dt || (this.sunsetTime < dt && this.nextSunriseTime > dt)) {

          return `<img src="./weatherIcons/night-clouds.svg" alt="曇り">`
        } else {
          return `<img src="./weatherIcons/Clouds.svg" alt="曇り">`
        }
      } else if(weather == 'Clear') {
        if(this.sunriseTime > dt || (this.sunsetTime < dt && this.nextSunriseTime > dt)) {
          return `<img src="./weatherIcons/night-clear.svg" alt="晴れ">`
        } else {
          return `<img src="./weatherIcons/Clear.svg" alt="晴れ">`
        }
      } else if(weather == 'Rain' || weather == 'Drizzle') {
        return `<img src="./weatherIcons/Rain.svg" alt="雨">`
      } else if(weather == 'Thunderstorm') {
        return `<img src="./weatherIcons/Thunderstorm.svg" alt="雷雨">`
      } else if(weather == 'Snow') {
        return `<img src="./weatherIcons/Snow.svg" alt="雪">`
      } else {
        return `<img src="./weatherIcons/Atmosphere.svg" alt="その他">`
      }
    }
  },
  mounted() {
    this.$store.dispatch('getHourlyWeatherData')
  },
}
</script>

