<template>
<div id="sunrs">
  <!-- sunsetがtrueの時 = 日の出時間以降〜日没時間まではsunset.svgを表示 -->
  <img v-if="sunset" src="@/assets/icons/sunset.svg" alt="日没">
  <!-- 上記以外の時 = 日没時間以降〜日の出時間まではsunrise.svgを表示 -->
  <img v-else src="@/assets/icons/sunrise.svg" alt="日の出">
  <p>{{ sunRSTime }}</p>
</div>
</template>

<script>
export default {
  data() {
    return {
      sunset: '',
      sunRSTime: ''
    }
  },
  created() {
    let that = this
    setInterval(that.getRSTime, 500)
  },
  computed: {
    sunsetTime() {
      return this.$store.state.sunsetTime
    },
    sunriseTime() {
      return this.$store.state.sunriseTime
    },
    nextSunriseTime() {
      return this.$store.state.nextSunriseTime
    },
  },
  methods: {
    getRSTime() { // 日の出、日没時間の取得
      let now = new Date()
      let a = now.getTime()
      let nowUnix = Math.floor(a / 1000) // 現在時刻をunixtimeに変換
      if(this.sunsetTime - nowUnix < 0) {
        // 日没時間を過ぎたら、翌日の日の出時間をsunRSTimeに格納
        let nextriseTime = new Date(this.nextSunriseTime * 1000)
        this.sunRSTime = nextriseTime.toLocaleTimeString('ja-JP').slice(0, -3)
        this.sunset = false
      } else if (nowUnix > this.sunriseTime) {// 日の出時間を過ぎたら、日没時間をsunRSTimeに格納
        let setTime = new Date(this.sunsetTime * 1000)
        this.sunRSTime = setTime.toLocaleTimeString('ja-JP').slice(0, -3)
        this.sunset = true
      } else { // 0時になったら、その日の日の出時間をsunRSTimeに格納
        let riseTime = new Date(this.sunriseTime * 1000)
        this.sunRSTime = riseTime.toLocaleTimeString('ja-JP').slice(0, -3)
        this.sunset = false
      }
    },
  }
}
</script>

<style scoped>
#sunrs {
  display: flex;
  justify-content: center;
  height: 50px;
  font-size: 4vh;
}
#sunrs img {
  width: 50px;
}
#sunrs p {
  line-height: 50px;
}
/* デバイスが横向きの時のスタイル */
@media (orientation: landscape) {
  #sunrs {
    font-size: 7vh;
  }
}
</style>
